croppedImageResult(type, image) {
  if (this.classMainFile) {
    const index = this.classFileList.indexOf(this.classMainFile);
    this.classFileList.splice(index, 1);
  }
  this.classMainFile = image;
  this.classFileList.push(this.classMainFile);
  this.classfilelength = false;
  this.src = image.base64;
  this.classform.patchValue({classImage:this.src});
  }
uploadClassPic(classMedia: any) {
    let objectDataid;
    if (classMedia) {
      objectDataid = classMedia.mediaId;
    } else {
      objectDataid = this.classResponse.classId;
    }
    const formData = new FormData();
    if (!this.classFileList.length) { return; }

    this.classMainFile.name = Date.now().toString().concat(this.classMainFile.extension);
    this.classMainFile.objectType = 'class';
    this.classMainFile.mediaFor = 'class';
    this.classMainFile.mediaType = 'Image';
    this.classMainFile.objectDataId = objectDataid;
    if (this.classResponse.classMedia) {
      this.http_user.updateMediaJS(objectDataid, this.classMainFile, this, true);
    } else {
      this.http_user.uploadMediaJS(this.classMainFile, this, true);
    }

  }
setupPlacesListResponse(responsedata: any) {
  this.allPlacesList = responsedata.data;
  this.disabledLocation = false;
  this.placeholder = 'Location';
  if (this.allPlacesList.length == 0) {
    this.disabledLocation = true;
    this.placeholder = 'Pending location approval';
  }
  this.allPlacesList.forEach(element => {
          element.localityName = element.gymName + ', '+   element.branchName + ', ' + element.locationName;;
        });
        this.dataService = this.completerService.local(this.allPlacesList, 'localityName', 'localityName');
        if (this.dataService['_data'].length == 0) {
        
      }
}
    selectClassBranch(event) {
      const value = this.classform.value.venue;
      this.placeid = null;
      if (event) {
        this.placeid = event.originalObject.branchId;
       } else if (event == undefined && value) {
        for (const val of this.allPlacesList) {
          if (value === val.localityName) {
            this.placeid=val.branchId;
            break;
          }
        }
      }
      if(this.placeid&&this.placeid>0){
        this.setVenue(this.placeid);
        this.trainerListByGymBranch(this.placeid);
      }
      else{
        this.classform.patchValue({venue:''});
        this.setVenue('');

      }
    }
    setVenue(placeid){
      this.classform.patchValue({venueid:placeid});
    }
  trainerListByGymBranch(placeid: number) {
      this.trainerId = null;
      if (LocalStorageService.getUserType() == 3 || LocalStorageService.getUserType() == 6) {
        this.http_common.getTrainerList('&branchId=' + placeid + '&gymId=' + LocalStorageService.getUserId(),
          this,
          true)
      }
    }
    goForword(){
      switch(this.currentStep){
        case 1:
          if(this.classform.value.platform!='' && this.classform.value.platform!='INAPP' ){
            this.classform.controls['link'].setValidators([Validators.required]);
            this.classform.controls['link'].updateValueAndValidity();
          }
          if(this.classform.valid){
            this.currentStep=2;
            if(!this.scheduleList||this.scheduleList.length==0)
            this.addnewschedule();
          }
          else{
            this.errorPopupModalMessage("","Please complete step one")
          }
          break;
          case 2:
              if(this.checkNoEmptyScheduleForTrainer()){
            if(this.checkIncompleteSlotInAll()){
              this.checkOverlapForSlots();
              this.currentStep=3;
            }
            else{
              this.errorPopupModalMessage("","Please complete filling all slots before proceeding");
            }
          }
              break;
              case 3:
             break;  
      }
      window.scroll(0, 0);

    }
    goToStep(step){
     if(step==1){
      this.currentStep=1;
      }
     if(step==2){
      if(this.classform.valid){
        this.currentStep=2;
        if(!this.scheduleList||this.scheduleList.length==0)
        this.addnewschedule();

      }
      else{
        this.errorPopupModalMessage("","Please complete step one")
      }  
    }
    if(step==3){
      if(this.classform.valid){
        if(this.checkNoEmptyScheduleForTrainer()){
          if(this.checkIncompleteSlotInAll()){
            this.checkOverlapForSlots();
            this.currentStep=3;
            window.scroll(0, 0);

          }else{
            this.errorPopupModalMessage("","Please complete filling all slots before proceeding");

          }
        }
        }
      else{
        this.errorPopupModalMessage("","Please complete filling all required feilds brfore proceeding");

      }
    } // Step 3 ends
    }
    goBackword(){
      switch(this.currentStep){
        case 1:
          break;
          case 2:
              this.currentStep=1;
          break;
              case 3:
                this.currentStep=2;
                  break;  
      }
    }
