addnewschedule(){ 
  if(this.checkIncompleteScheduleInAll()){
if(this.checkIncompleteSlotInAll()){
  let that= this;
let ScheduleListObject:ScheduleList={"daterangeId":this.scheduleList.length+"","fromDate":"","toDate":"","daysList":[],"isEnabled":true,"isCurrentDay":false,"activeDaysName":"","isDeleted":false,"isExpanded":true};
this.scheduleList.push(ScheduleListObject);
var daterangeIdentifier ="input[name=daterange"+ScheduleListObject.daterangeId+"]";
$(function() {
  $(daterangeIdentifier).daterangepicker({ 
    autoUpdateInput: false,
    drops: "down",
    locale: {
        format: 'YYYY-MM-DD',
        cancelLabel: 'Close'

    },
     minDate:new Date(),
},function(start, end, label) {
    $(daterangeIdentifier).val(start.format('YYYY-MM-DD') + ' - ' + end.format('YYYY-MM-DD'));
     that.recreateSlots(start.format('YYYY-MM-DD'),end.format('YYYY-MM-DD'),ScheduleListObject);
    });
  });
  let accIdentifier = "#acc"+ (+ScheduleListObject.daterangeId - 1);
    $(accIdentifier).collapse('hide');
}else{
  this.errorPopupModalMessage("","Please complete filling all slots before adding new schedule");
}
  }
  else {
    this.errorPopupModalMessage("","Please complete current schedule before adding new schedule");
  }
}

haldleaccordion(scheduleIndex){
this.scheduleList.forEach(element => {
  if(element.daterangeId!=scheduleIndex){
    this.scheduleList[element.daterangeId].isExpanded=false;
    let accIdentifier = "#acc"+ (+element.daterangeId);
    $(accIdentifier).collapse('hide');
  }
 
});
if(this.scheduleList[scheduleIndex].isExpanded){
  this.scheduleList[scheduleIndex].isExpanded=false;
  let accIdentifier = "#acc"+ (+this.scheduleList[scheduleIndex].daterangeId);
  $(accIdentifier).collapse('hide');
}else{
  this.scheduleList[scheduleIndex].isExpanded=true;
  let accIdentifier = "#acc"+ (+this.scheduleList[scheduleIndex].daterangeId);
  $(accIdentifier).collapse('show');
}
}

