recreateSlots(start,end,ScheduleListObject){ 
  let availableSlotsList = UserSession.getAllSessionsOfSchedule(this.scheduleList[ScheduleListObject.daterangeId].daysList); 
  this.prepareScheduleData(start,end,ScheduleListObject.daterangeId);
  this.addSlotsTodeletedlist(availableSlotsList,ScheduleListObject); 
     this.scheduleList[ScheduleListObject.daterangeId].daysList.forEach(elementDay => {
      const filteredSlots = availableSlotsList.filter((element) => {
        return DayListEnum[element.classDay] === DayListEnum[elementDay.dayNumber];
      });
      filteredSlots.forEach(element => {
        this.autoFillExistingSlot(ScheduleListObject.daterangeId,this.scheduleList[ScheduleListObject.daterangeId].daysList.indexOf(elementDay),element);
      });
    }); 

}
addSlotsTodeletedlist(availableSlotsList,ScheduleListObject){
  availableSlotsList.forEach(element => {
    let isdeletable=true;
    this.scheduleList[ScheduleListObject.daterangeId].daysList.forEach(elementDay => {
      if(element.classDay == elementDay.dayNumber){
        isdeletable=false;
       }
    });
    if(isdeletable && element.classTimingId && element.classTimingId!=0 ){
      this.deletedClassTimingIds.push(element.classTimingId)
    }
  });

}
private deleteSchedule(scheduleIndex){
     this.confirmationService.confirm({
      message: 'Are you sure that you want to delete this schedule? All slots in this schedule will be deleted after you submit the details in last step.',
      header:  'Alert',
      acceptLabel:'Delete',
      rejectLabel:"Cancel",
      accept: () => {
        this.scheduleList[scheduleIndex].isDeleted=true;
        this.scheduleList[scheduleIndex].daysList.forEach(element => {
          element.slotList.forEach(elementSlot => {
            if(elementSlot.classTimingId && elementSlot.classTimingId>0 ){
              this.deletedClassTimingIds.push(elementSlot.classTimingId);
            } 
            
          });
          
        });
     },
      blockScroll : false,
      
  });



 
}

checkNoEmptyScheduleForTrainer(){
  if(this.userTypeLogedIn==2) {
    this.getAllSessions();
    if(this.allSlotList.length>0) { return true; }
    else { this.errorPopupModalMessage("","You cannot proceed with a completely empty schedule");
  return false; }
  }
  else { 
    return true; }
}

getMaxToDateOfClass() {
  var dates:any[] = [];
   this.scheduleList.forEach(element => {
    dates.push(new Date(element.toDate));   
   });
 
    this.maxToDate = JSON.stringify(new Date(Math.max.apply(null, dates))).slice(1,11);

 }
 
private prepareScheduleData(selectedFromDate: string, selectedToDate: string,index) {
const fromDate: any = new Date(selectedFromDate);
const toDate: any = new Date(selectedToDate);
const days = Math.round(toDate - fromDate) / (60 * 60 * 24 * 1000);
let preparedData: Array<any> = new Array<any>();
let enabledDays: Array<boolean> = [false, false, false, false, false, false, false];
if (days >= 7) {
  enabledDays = [true, true, true, true, true, true, true];
} else {
  const fromDateDay: number = fromDate.getDay();
  const toDateDay: number = toDate.getDay();
  enabledDays[fromDateDay] = true;
  enabledDays[toDateDay] = true;
  for (let index = 1; index < days; index++) {
    if (fromDateDay + index <= 6) {
      enabledDays[fromDateDay + index] = true;
    } else {
      enabledDays[(fromDateDay + index) - 7] = true;
    }
  }
}
let activeDaysName='';
for (let index = 0; index < 7; index++) {
  if (enabledDays[index]) {
    activeDaysName = activeDaysName+DayListEnumSort[(index + 1)]+', ';
  }
}
this.scheduleList[index].fromDate=selectedFromDate;
this.scheduleList[index].toDate=selectedToDate;

this.scheduleList[index].activeDaysName=activeDaysName.trim().substring(0,activeDaysName.trim().length-1);

preparedData = this.prepareSlotList(enabledDays,selectedFromDate,selectedToDate,index);
this.scheduleList[index].daysList=preparedData;

const date = new Date(selectedFromDate);
const date2 = new Date();

 if (selectedFromDate == selectedToDate && date.getDate() == date2.getDate() && date.getMonth() == date2.getMonth() && date.getFullYear() == date2.getFullYear()) {
    this.scheduleList[index].isCurrentDay=true;
} else {
  this.scheduleList[index].isCurrentDay = false;
 }
}

prepareSlotList(enabledDays: Array<boolean>,selectedFromDate: string, selectedToDate: string,scheduleIndex): Array<any> {
const preparedList: Array<any> = [];
for (let index = 0; index < 7; index++) {
    if (enabledDays[index]) {
      preparedList.push({
        fromDate:selectedFromDate,
        toDate:selectedToDate,
        dayName: DayListEnum[(index + 1)],
        dayNumber: index + 1,
        slotList: [],
        isEnabled: enabledDays[index]
      });
    }
  }
return preparedList
}
