validate(scheduleIndex: number, daysIndex: number, validationType: ValidationTypeModel): boolean {
let result: boolean;
switch (validationType) {

  case ValidationTypeModel.CopyToAll:
  
    break;
  case ValidationTypeModel.Overlap:
    break;
  case ValidationTypeModel.EmptySlots:
    break;
  case ValidationTypeModel.IsIncomplete:
  result = this.isSlotIncomplete(scheduleIndex,daysIndex);
    break;

}

return result;
}
private isSlotIncomplete(scheduleIndex: number, daysIndex: number): boolean {
if (this.scheduleList[scheduleIndex].daysList[daysIndex].slotList.length != 0) {
  for (let index = 0; index < this.scheduleList[scheduleIndex].daysList[daysIndex].slotList.length; index++) {
    if (this.scheduleList[scheduleIndex].daysList[daysIndex].slotList[index].durationMinutes == 0||this.scheduleList[scheduleIndex].daysList[daysIndex].slotList[index].duration == 0 || this.scheduleList[scheduleIndex].daysList[daysIndex].slotList[index].duration == "00:00" || this.scheduleList[scheduleIndex].daysList[daysIndex].slotList[index].fromTime == '') {
      return false;
    }
  }
}
return true;
}

private getTwentyFourHourTime(time: string | Date): string {
  if (time == undefined || time == null || time == '') {
    return;
  }
  if (typeof (time) === "string") {
    let timeSplit: any = time.split(":");
    timeSplit[1] = timeSplit[1].split(" ");
    if (timeSplit[1][1] == "PM" && parseInt(timeSplit[0])<12) {
      timeSplit[0] = parseInt(timeSplit[0]) + 12;
    } if (timeSplit[1][1] == "AM" && parseInt(timeSplit[0])==12) {
      timeSplit[0] = 0;
    }
    return timeSplit[0] + ":" + timeSplit[1][0] + ":00";
  }
  else {
    return time.getHours() + ":" + time.getMinutes() + ":00";
  }
}
formatDateToAMPM(date) {
var hours = date.getHours();
var minutes = date.getMinutes();
var ampm = hours >= 12 ? 'PM' : 'AM';
hours = hours % 12;
hours = hours ? hours : 12; // the hour '0' should be '12'
minutes = minutes < 10 ? '0'+minutes : minutes;
var strTime = hours + ':' + minutes + ' ' + ampm;
return strTime;
}
convertDurationToMinutes(selecteddDuration){
let selectedTime = selecteddDuration.split(':');
let hoursSelected = selectedTime[0];
let minutesSelected = selectedTime[1];
let totalMinutes = hoursSelected*60 + +minutesSelected;
return totalMinutes;
}
addMinutes(date, minutes) {
return new Date(date.getTime() + minutes*60000);
}
pad(n) {
return (n < 10) ? ("0" + n) : n;
}
