getAllSessions():ClassDayListNew[]{
  this.allSlotList=[];
  this.allSessions=[];
  this.scheduleList.forEach(element => {
    if(!element.isDeleted){
    element.daysList.forEach(dayelement=>{
      dayelement.slotList.forEach(slotElement=>{
        slotElement.fromDate=element.fromDate;
        slotElement.toDate=element.toDate;
        this.allSlotList.push(slotElement);
      })
    });
  }
    });
    return this.allSlotList;
}
private checkIncompleteSlotInAll(): boolean {
if (this.scheduleList.length != 0) {
  let allSession=this.getAllSessions()
  for (let index = 0; index < allSession.length; index++) {
    if (allSession[index].durationMinutes == 0||allSession[index].duration == 0 || allSession[index].duration == "00:00" || allSession[index].fromTime == '') {
      return false;
    }
  }
}
return true;
}

private checkIncompleteScheduleInAll(): boolean {
  const filteredSchedule = this.scheduleList.filter((element) => {
    return !element.isDeleted;
  });
  if (filteredSchedule.length != 0) {
    let count = 0;
    filteredSchedule.forEach(element => {
      element.daysList.forEach(elementDaysList => {
        count += elementDaysList.slotList.length;
      });
    });
    if(count==0){
        return false;
    } else{
      return true;

    } 
   
  }
  else{
    return true;

  }
}

get activeSchedules(){
  const filteredSchedule = this.scheduleList.filter((element) => {
    return !element.isDeleted;
  });
  return filteredSchedule;
}

checkOverlapForSlots(){
  if(this.checkIncompleteSlotInAll()){
    this.getAllSessions();
    this.createSessionsFor7Days();
    this.checkOverlap();
    this.markslotsOverlap();
  }else{
    this.errorPopupModalMessage("","Please complete filling all slots before proceeding");
  }
 }
markslotsOverlap(){
this.scheduleList.forEach(element => {
  if(!element.isDeleted){
  element.daysList.forEach(dayelement=>{
      dayelement.slotList.forEach(slotElement=>{
        if(this.overlapingSlots.indexOf(slotElement.slotId)>-1){
          slotElement.isOverlapingOtherSlots=true;
        }else{
          slotElement.isOverlapingOtherSlots=false;
        }
      })
    });
  }
    }); 
    
}

createSessionsFor7Days(){
for(let i=0;i<this.scheduleList.length;i++){
let dates:Date[] = this.getDatesListWhitinRange(this.scheduleList[i].fromDate,this.scheduleList[i].toDate);
let looplength = dates.length<8?dates.length:8;
this.allSlotList.forEach(element => {
  for(let j=0;j<looplength;j++){
    let elementDate = dates[j];
    if((element.scheduleIndex == i)&&(DayListEnumStandard[elementDate.getDay()] == DayListEnum[element.classDay])){
      let dateString =elementDate.getMonth()+1 +'/'+elementDate.getDate()+'/'+elementDate.getFullYear()+' ';
        element.sessionFromTime=new Date(dateString + element.fromTime24HourFormat);
        element.sessionToTime=new Date(dateString + element.toTime24HourFormat);
        this.allSessions.push(element);
      }
  }
});
}
}
checkOverlap(){
this.overlapingSlots=[];
this.allSessions.forEach(element => {
  this.allSessions.forEach(element1=>{
      if(element.slotId!=element1.slotId && element.trainerId && element.trainerId==element1.trainerId){
        if(!this.overlap(element.sessionFromTime.getTime(),element1.sessionFromTime.getTime(),element.sessionToTime.getTime(),element1.sessionToTime.getTime())){
          if(this.overlapingSlots.indexOf(element.slotId)==-1){
            this.overlapingSlots.push(element.slotId);

          }
          if(this.overlapingSlots.indexOf(element1.slotId)==-1){
            this.overlapingSlots.push(element1.slotId);
      }
    }
      }
});
  });
}
overlap(fromtime1,fromtime2,endtime1,endtime2){
if((fromtime1>fromtime2&&fromtime1<endtime2)||(endtime1>fromtime2&&endtime2>endtime1)||(fromtime2==fromtime1)){
  return false;
}
else if((fromtime2>fromtime1&&endtime2<fromtime1)||(fromtime2 >endtime1&&endtime1 >endtime2)||(fromtime2==fromtime1)){
  return false;
}
return true;
}
getDatesListWhitinRange(startDate,endDate){
  var dates:Date[] = [];
  const fromDate: any = new Date(startDate);
  const toDate: any = new Date(endDate);
  const days = Math.round(toDate - fromDate) / (60 * 60 * 24 * 1000);
  dates.push(new Date(fromDate.setDate(fromDate.getDate() + 0)));

  for(let i=0;i<days;i++){
  dates.push(new Date(fromDate.setDate(fromDate.getDate() + 1)));
  }
  return dates;
}
